* {
    margin: 0;
    box-sizing: border-box;
}
nav {
    background-color: darkgray;
    position: fixed;
    width: 100%;
    padding: 5px;

    a {
        color: white;
        text-decoration: none;
        padding: 5px;
    }
}



.header {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-image: url('/img/welcome.jpg');
    width: 100%;
    height: 50vh;

    h1 {
       font-size: 400%; 
       padding-bottom: 5px;
       color: white;
       padding-top: 80px;
    }
    h2 {
        padding-bottom: 50px;
        color: white;
    }
}
.plot > .plotcontent {
    width: 800px;
    margin: 0 auto;
}
.plot {
    background-color: lightgray;
    display: flex;
    flex-direction: column;
    padding-top: 100px;

    h2 {
        text-align: center;
        font-size: 200%;
        padding-bottom: 40px;
    }
}
.plot img {
    height: 30%;
    width: 30%;
    padding-right: 5px;
    padding-bottom: 50px;
}
.plotcontent {
    display: flex;
    flex-direction: row;
    justify-content: center;
}
.plotcontentwords {
    display: flex;
    flex-direction: column;
    padding-left: 50px;

    h3 {
        padding-top: 50px;
        padding-bottom: 50px;
    }
    p {
        line-height: 130%;
    }
}



.characters > 
.characters img {
    border-radius: 50%;
}
.characters {
    display: flex;

    background-image: url('/img/rey-standing.jpg');
}